.ReactModal__Overlay {
  z-index: 1;
  position: fixed;
  top: 0px;
  left: 0px;
  right: 0px;
  bottom: 0px;
  background-color: #FFFFFF;
  overflow-y: auto;
  .close-button {
    background-color: #FFFFFF;
    position: fixed;
    top: 10px;
    right: 10px;
    padding: 10px;
    cursor: pointer;

    .image {
      width: 20px;
      height: 20px;
      background-image: url(../../asset/image/x@2x.png);
      background-position: center;
      background-repeat: no-repeat;
      background-size: contain;
    }
  }
}
.ReactModal__Content {
  position: relative;
  padding: 25px;
}



